@import "../../assets/scss/app.scss";

.team {
  padding: vw0(80) vw0(20) vw0(290);
  background-image: url(../../assets/png/team/back-mobile.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  @include mobile() {
    padding: vwM(80) vwM(20) vwM(290);
  }

  @include desktop() {
    padding: vwD(100) vwD(140) vwD(333);
    background-image: url(../../assets/png/team/back-desktop.png);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }

  .content {
    margin-top: vw0(30);
    display: grid;
    grid-template-rows: repeat(4, 1fr);
    grid-row-gap: vw0(43);

    @include mobile() {
      margin-top: vwM(30);
      grid-row-gap: vwM(43);
    }

    @include desktop() {
      margin-top: vwD(50);
      grid-template-rows: repeat(2, 1fr);
      grid-template-columns: repeat(2, 1fr);
      grid-row-gap: vwD(60);
      grid-column-gap: vwD(40);
    }

    .card {
      padding: vw0(20) vw0(30);
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      background: #FFFFFF;
      border-radius: vw0(16);
      position: relative;

      @include mobile() {
        padding: vwM(20) vwM(30);
        border-radius: vwM(16);
      }

      @include desktop() {
        padding: vwD(28) vwD(60) vwD(25);
        border-radius: vwD(16);
      }

      &::before {
        position: absolute;
        z-index: 0;
        content: "";
        display: block;
        top: vw0(10);
        left: vw0(12);
        right: vw0(12);
        bottom: vw0(-8);
        background: #FFFFFF;
        opacity: 0.5;
        border-radius: vw0(12);

        @include desktop() {
          top: vwD(10);
          left: vwD(12);
          right: vwD(12);
          bottom: vwD(-10);
          border-radius: vwD(12);
        }
      }

      .person {
        position: absolute;
        bottom: 0;
        left: 0;
        width: vw0(140);
        height: vw0(140);

        @include desktop() {
          width: vwD(200);
          height: vwD(200);
        }
      }

      .lizardMobile {
        @include onlyMobile() {
          position: absolute;
          top: vw0(-32);
          right: vw0(-10);
          width: vw0(64);
          height: vw0(64);
        }
      }

      .lizardDesktop {
        @include onlyDesktop() {
          position: absolute;
          top: vwD(-80);
          right: vwD(-50);
          width: vwD(133);
          height: vwD(133);
        }
      }

      .inner {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        position: relative;
        z-index: 1;

        .name {
          @include Impact();
          font-size: vw0(20);
          line-height: 130%;
          text-transform: uppercase;
          width: vw0(134);
          //margin-top: vw0(8);

          @include mobile() {
            font-size: vwM(20);
            width: vwM(134);
            //margin-top: vwM(8);
          }

          @include desktop() {
            font-size: vwD(24);
            line-height: 160%;
            width: vwD(270);
            //margin-top: vwD(10);
          }
        }

        .bottom {
          display: flex;
          align-items: center;
          margin-top: vw0(8);

          @include mobile() {
            margin-top: vwM(8);
          }

          @include desktop() {
            margin-top: vwD(9);
          }

          .job {
            @include Rubik();
            font-size: vw0(13);
            line-height: 160%;
            padding: 0 vw0(10);
            border-radius: vw0(28);

            @include mobile() {
              font-size: vwM(13);
              padding: 0 vwM(10);
              border-radius: vwM(28);

            }

            @include desktop() {
              font-size: vwD(13);
              padding: 0 vwD(10);
              border-radius: vwD(28);
            }
          }

          .readMoreMobile {
            @include onlyMobile(){
              cursor: pointer;
              @include Impact();
              font-size: vw0(12);
              text-transform: uppercase;
              margin-left: vw0(16);
              transition: 0.3s;

              &:hover {
                color: #575757;
              }
            }
          }

          .readMoreDesktop {
            @include onlyDesktop() {
              cursor: pointer;
              @include Impact();
              font-size: vwD(14);
              text-transform: uppercase;
              margin-left: vwD(20);
              transition: 0.3s;

              &:hover {
                color: #575757;
              }
            }
          }

          .toolTipWrapper {
            background: none!important;
            background: #FFFFFF!important;
            opacity: 1!important;
            box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.3)!important;
            border-radius: vw0(10)!important;
            padding: vw0(12)!important;
            width: vw0(268)!important;
            box-sizing: border-box;
            z-index: 999;

            @include desktop() {
              padding: vwD(14)!important;
              border-radius: vwD(10)!important;
              width: vwD(308)!important;
            }

            .toolTip {
              font-family: 'Rubik';
              font-style: normal;
              font-weight: 400;
              font-size: vw0(12);
              line-height: 160%;
              color: #424242;
              width: vw0(244)!important;

              @include desktop() {
                font-size: vwD(13);
                width: vwD(280)!important;
              }
            }
          }

          .muiTooltip {

          }


        }



      }


    }


  }


}