@import "../../assets/scss/app.scss";

.rarity {
  padding: vw0(80) vw0(20) vw0(12);
  background: url(../../assets/png/rarity/back-mobile.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  @include mobile() {
    //padding: 80px 20px 12px;
    padding: vwM(80) vwM(20) vwM(12);
  }

  @include desktop() {
    padding: vwD(100) 0px;
    background: url(../../assets/png/rarity/back-desktop.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }

  .content {
    background: #FFFFFF;
    border-radius: vw0(16);
    box-sizing: border-box;
    padding: vw0(20);
    display: flex;
    flex-direction: column;
    margin-top: vw0(40);

    @include mobile() {
      border-radius: vwM(16);
      padding: vwM(20);
      margin-top: vwM(40);
    }

    @include desktop() {
      width: vwD(960);
      padding: vwD(40);
      border-radius: vwD(16);
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-column-gap: vwD(40);
      margin: vwD(73) auto 0;
    }

    .info {

      .item {
        display: flex;
        justify-content: space-between;
        align-items: center;

        &:not(:first-child) {
          margin-top: vw0(10);
          padding-top: vw0(10);
          border-top: 1px solid #EFEACB;

          @include mobile() {
            margin-top: vwM(10);
            padding-top: vwM(10);
          }

          @include desktop() {
            margin-top: vwD(16);
            padding-top: vwD(16);
          }
        }

        .label {
          @include Rubik();
          font-size: vw0(16);
          line-height: 160%;
          color: #424242;

          @include mobile() {
            font-size: vwM(16);
          }

          @include desktop() {
            font-size: vwD(20);
          }
        }

        .value {
          @include Impact();
          font-size: vw0(20);
          line-height: 160%;
          text-transform: uppercase;

          @include mobile() {
            font-size: vwM(20);
          }

          @include desktop() {
            font-size: vwD(24);
          }
        }
      }
    }

    .img {
      display: block;
      width: vw0(280);
      height: vw0(280);
      margin-top: vw0(20);
      align-self: center;

      @include mobile() {
        width: vwM(280);
        height: vwM(280);
        margin-top: vwM(20);
      }

      @include desktop() {
        margin-top: 0;
        width: vwD(420);
        height: vwD(420);
      }
    }
  }
}