@import "../../assets/scss/app.scss";

.nft {
  padding: vw0(131) vw0(20) vw0(234);
  background: url(../../assets/png/nft/back-mobile.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @include mobile() {
    padding: vwM(131) vwM(20) vwM(234);
  }

  @include desktop() {
    padding: vwD(157) 0 vwD(705);
    background: url(../../assets/png/nft/back-desktop.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

  }

  .content {

    @include desktop() {
      width: vwD(524);
      margin: 0 auto;
    }

    .title {
      @include Impact();
      font-size: vw0(46);
      line-height: 150%;
      text-align: center;
      text-transform: uppercase;

      @include mobile() {
        font-size: vwM(46);
      }

      @include desktop() {
        font-size: vwD(80);
      }
    }

    .text {
      @include Rubik();
      font-size: vw0(14);
      line-height: 160%;
      text-align: center;
      margin-top: vw0(14);

      @include mobile() {
        font-size: vwM(14);
        margin-top: vwM(14);
      }

      @include desktop() {
        font-size: vwD(14);
        margin-top: vwD(10);
      }
    }

    .buttons {
      display: flex;
      justify-content: space-between;
      margin-top: vw0(24);

      @include mobile() {
        margin-top: vwM(24);
      }

      @include desktop() {
        width: vwD(380);
        margin-left: auto;
        margin-right: auto;
        margin-top: vwD(30);
      }
    }


  }
}