@import "../../../assets/scss/app.scss";

.buttonPlusMinus {
  background: linear-gradient(0deg, #FFFFFF, #FFFFFF), #E9E9E9;
  border: 1px solid #1E1A3A;
  box-sizing: border-box;
  box-shadow: 1px 4px 0px #332C62, inset -4px -4px 2px rgba(0, 0, 0, 0.11), inset 4px 4px 2px rgba(255, 255, 255, 0.4);
  border-radius: vw0(28);
  height: vw0(44);
  width: vw0(44);
  transition: 0.3s opacity;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;

  @include mobile() {
    border-radius: vwM(28);
    height: vwM(44);
    width: vwM(44);
  }

  @include desktop() {
    border-radius: vwD(28);
    height: vwD(50);
    width: vwD(50);
  }

  &:hover {
    background: #FFE921;
  }

  &_mouseDown {
    background: linear-gradient(0deg, #EDD81B, #EDD81B), #E9E9E9 !important;
  }

  &:disabled {
    opacity: 0.5;
    background: linear-gradient(0deg, #FFFFFF, #FFFFFF), #E9E9E9!important;
    cursor: auto;
  }

  p {
    @include Rubik();
    font-weight: 700;
    font-size: vw0(24);
    line-height: 160%;
    text-transform: uppercase;
    color: #000;

    @include mobile() {
      font-size: vwM(24);
    }

    @include desktop() {
      font-size: vwD(24);
    }
  }

}