@import "../../../assets/scss/app.scss";

.buttonIcon {
  background: linear-gradient(0deg, #FFDB33, #FFDB33), linear-gradient(0deg, #00A6E2, #00A6E2), #E9E9E9;
  border: 1px solid #1E1A3A;
  box-sizing: border-box;
  box-shadow: 1px 4px 0px #332C62, inset -4px -4px 2px rgba(0, 0, 0, 0.11), inset 4px 4px 2px rgba(255, 255, 255, 0.4);
  border-radius: vw0(28);
  height: vw0(44);
  width: vw0(44);
  transition: 0.3s;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;

  @include mobile() {
    border-radius: vwM(28);
    height: vwM(44);
    width: vwM(44);
  }

  @include desktop() {
    border-radius: vwD(28);
    height: vwD(44);
    width: vwD(44);
  }

  svg {
    height: vw0(16);
    width: vw0(16);

    @include mobile() {
      height: vwM(16);
      width: vwM(16);
    }

    @include desktop() {
      height: vwD(16);
      width: vwD(16);
    }
  }
}