@import "../../../assets/scss/app.scss";

.buttonJoin {
  text-decoration: none;
  background: linear-gradient(0deg, #7179EF, #7179EF), linear-gradient(0deg, #00A6E2, #00A6E2), #E9E9E9;
  border: 1px solid #1E1A3A;
  box-sizing: border-box;
  box-shadow: 1px 4px 0px #332C62, inset -3px -3px 3px rgba(0, 0, 0, 0.1), inset 3px 3px 3px rgba(255, 255, 255, 0.3);
  border-radius: vwM(28);
  height: vwM(50);
  width: vwM(154);
  //transition: 0.3s;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;

  @include mobile() {
    border-radius: vwM(28);
    height: vwM(50);
    width: vwM(154);
  }

  @include desktop() {
    height: vwD(50);
    width: vwD(180);
    border-radius: vwD(28);
  }

  &:hover {
    background: linear-gradient(0deg, #6B95FF, #6B95FF), linear-gradient(0deg, #00A6E2, #00A6E2), #E9E9E9;
  }

  &_mouseDown {
    background: linear-gradient(0deg, #5A63EB, #5A63EB), linear-gradient(0deg, #00A6E2, #00A6E2), #E9E9E9!important;
  }

  svg {
    width: vw0(20);
    height: vw0(20);

    @include mobile() {
      width: vwM(20);
      height: vwM(20);
    }

    @include desktop() {
      width: vwD(24);
      height: vwD(24);
    }
  }

  p {
    text-decoration: none;
    @include Impact();
    font-size: vw0(14);
    line-height: 130%;
    text-transform: uppercase;
    color: #FFFFFF;
    margin-left: vw0(5);

    @include mobile() {
      font-size: vwM(14);
      margin-left: vwM(5);
    }

    @include desktop() {
      margin-left: vwD(10);
      font-size: vwD(16);
    }
  }


}