@import "../../../assets/scss/app.scss";

.buttonMint {
  background: linear-gradient(0deg, #FCD349, #FCD349), #E9E9E9;
  border: 1px solid #1E1A3A;
  box-sizing: border-box;
  box-shadow: 1px 4px 0px #332C62, inset -3px -3px 3px rgba(0, 0, 0, 0.1), inset 3px 3px 3px rgba(255, 255, 255, 0.3);
  border-radius: vw0(28);
  height: vw0(50);
  width: vw0(154);
  //transition: 0.3s;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;

  @include mobile() {
    border-radius: vwM(28);
    height: vwM(50);
    width: vwM(154);
  }

  @include desktop() {
    border-radius: vwD(28);
    height: vwD(50);
    width: vwD(180);
  }

  &:hover {
    background: #FFE921;
  }

  &_mouseDown {
    background: linear-gradient(0deg, #EDD81B, #EDD81B), #E9E9E9!important;
  }

  p {
    @include Impact();
    font-size: vw0(14);
    line-height: 130%;
    text-transform: uppercase;
    color: #000;

    @include mobile() {
      font-size: vwM(14);
    }

    @include desktop() {
      font-size: vwD(16);
    }
  }
}