@import "../../assets/scss/app.scss";

.modal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.21);
  z-index: 999;
  @include center();

  .content {
    position: relative;
    z-index: 2;
    background-image: url(../../assets/png/modal/back-mobile.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: vw0(320);
    height: vw0(523);
    padding: vw0(80) vw0(45) 0;
    border-radius: vw0(16);
    box-sizing: border-box;
    position: relative;

    @include desktop() {
      background-image: url(../../assets/png/modal/back-desktop.png);
      width: vwD(640);
      height: vwD(351);
      padding: vwD(52) vwD(80) 0;
      border-radius: vwD(16);
    }

    .btnClose {
      position: absolute;
      top: vw0(12);
      right: vw0(12);

      @include desktop() {
        top: vwD(20);
        right: vwD(25);
      }

    }

    .title {
      @include Impact();
      font-size: vw0(34);
      line-height: 150%;
      text-align: center;
      text-transform: uppercase;

      @include desktop() {
        font-size: vwD(40);
      }
    }

    .subtitle {
      margin-top: vw0(10);
      @include Rubik();
      font-size: vw0(14);
      line-height: 160%;
      text-align: center;
      color: #424242;

      @include desktop() {
        margin-top: vwD(10);
        font-size: vwD(15);
      }
    }

    .btns {
      margin-top: vw0(30);
      display: flex;
      flex-direction: column;
      align-items: center;

      @include desktop() {
        margin-top: vwD(30);
        flex-direction: row;
        justify-content: center;
      }

      button {
        cursor: pointer;
        border: none;
        outline: none;
        background: #FFF;
        width: vw0(230);
        height: vw0(115);
        @include center();
        border-radius: vw0(8);
        transition: 0.3s;

        @include desktop() {
          width: vwD(230);
          height: vwD(115);
          border-radius: vwD(8);
        }

        &:hover {
          box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.16);
        }

        &:not(:first-child) {
          margin-top: vw0(16);

          @include desktop() {
            margin-top: 0;
            margin-left: vwD(20);
          }
        }

        svg {
          width: vw0(48);
          height: vw0(48);

          @include desktop() {
            width: vwD(48);
            height: vwD(48);
          }

        }

        .label {
          margin-top: vw0(2);
          @include Impact();
          font-size: vw0(18);
          line-height: 150%;
          text-align: center;
          text-transform: uppercase;

          @include desktop() {
            margin-top: vwD(2);
            font-size: vwD(18);
          }
        }

      }
    }
  }
}