@import "../../assets/scss/app.scss";

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: vw0(8) vw0(20) vw0(8);
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 40;
  transition: 0.3s;

  &_isSliding {
    background: rgba(255, 255, 255, 0.6);
    backdrop-filter: blur(22px);
  }

  @include mobile() {
    padding: vwM(8) vwM(20) vwM(8);
  }

  @include desktop() {
    padding: vwD(11) vwD(140) vwD(11);
  }

  .logo {
    display: block;

    img {
      display: block;
      width: vw0(191);
      height: vw0(52);

      @include mobile() {
        width: vwM(191);
        height: vwM(52);
      }

      @include desktop() {
        width: vwD(204);
        height: vwD(55);
      }
    }
  }

  .links {
    @include onlyDesktop(flex) {

      .link {
        position: relative;
        text-decoration: none;

        &:not(:first-child) {
          margin-left: vwD(40);
        }

        &::after {
          z-index: 1;
          content: "";
          display: block;
          position: absolute;
          top: 0;
          bottom: 0;
          left: vwD(-7);
          right: vwD(-7);
          border-radius: vwD(50);
          background: rgba(255, 255, 255, 0.0);
          transition: 0.3s;
        }


        &:hover {
          &::after {
            z-index: 1;
            content: "";
            display: block;
            position: absolute;
            top: 0;
            bottom: 0;
            left: vwD(-7);
            right: vwD(-7);
            border-radius: vwD(50);
            background: rgba(255, 255, 255, 0.8);
          }
        }

        span {
          position: relative;
          z-index: 2;
          @include Rubik();
          font-size: vwD(16);
          line-height: 160%;
        }
      }
    }
  }

  .btnConnect {
    @include onlyDesktop(flex);
  }

  .btnBurger {
    @include onlyMobile(flex);
  }

  .account {
    @include onlyDesktop();
  }


}