@import "../../assets/scss/app.scss";

.roadmap {
  position: relative;
  overflow: hidden;
  padding: vw0(80) 0 vw0(210);
  background-image: url(../../assets/png/roadmap/back-mobile.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  @include mobile() {
    padding: vwM(80) 0 vwM(210);
  }

  @include desktop() {
    padding: vwD(100) vwD(240) vwD(210);
    background-image: url(../../assets/png/roadmap/back-desktop.png);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }

  .imgLeft {
    position: absolute;
    top: vw0(150);
    left: vw0(-30);
    width: vw0(134);
    height: vw0(628);

    @include desktop() {
      display: block;
      z-index: 30;
      top: auto;
      bottom: 0;
      left: 0;
      width: vwD(565);
      height: vwD(2134);
    }
  }

  .imgRight {
    position: absolute;
    width: vw0(228);
    height: vw0(262);
    bottom: 0;
    right: vw0(-72);
    //display: none;

    @include desktop() {
      display: block;
      z-index: 10;
      bottom: 0;
      right: 0;
      width: vwD(518);
      height: vwD(596);
    }
  }

  .cardsMobile {
    @include onlyMobile() {
      position: relative;
      margin-top: vw0(50);
      padding-left: vw0(110);

      .step {
        position: absolute;
        left: 0;
        top: 0;
        height: vw0(41);
        width: vw0(94);
        border: vw0(2) solid #000000;
        border-left: none;
        box-sizing: border-box;
        border-radius: 0 vw0(16) vw0(16) 0;
        transition: 0.3s;

        @include Impact();
        font-size: vw0(24);
        line-height: 160%;
        text-transform: uppercase;
        color: #FFF194;
        text-align: right;
        padding-right: vw0(30);
      }

      .swiper {

        .slide {
          position: relative;
          z-index: 2;
          width: vw0(220);
          height: vw0(444);
          padding-bottom: vw0(8);
          display: flex;

          .shadow {
            position: absolute;
            top: vw0(12);
            left: vw0(12);
            right: vw0(12);
            bottom: 0;
            background: #FFFFFF;
            opacity: 0.5;
            border-radius: vw0(12);
            z-index: 5;
          }

          .innerSlide {
            position: relative;
            z-index: 10;
            box-sizing: border-box;
            padding: vw0(16);
            background: #FFFFFF;
            border-radius: vw0(16);

            .title {
              @include Impact();
              font-size: vw0(20);
              line-height: 160%;
              text-transform: uppercase;
            }

            .text {
              margin-top: vw0(8);
              @include Rubik();
              font-size: vw0(14);
              line-height: 160%;
              color: #424242;
            }
          }


        }
      }

      .dots {
        position: absolute;
        left: vw0(126);
        bottom: vw0(-31);
        display: flex;

        .dot {
          width: vw0(12);
          height: vw0(12);
          border: vw0(1) solid #FFFFFF;
          box-sizing: border-box;
          border-radius: 50%;
          transition: 0.3s;

          &:not(:first-child) {
            margin-left: vw0(10);
          }

          &_selected {
            background: #FFF;
          }
        }

      }
    }
  }


  .cardsDesktop {
    @include onlyDesktop() {
      position: relative;
      z-index: 20;
      margin-top: vwD(50);

      .cardsBlock {
        display: flex;
        justify-content: flex-end;
        position: relative;

        &:not(:first-child) {
          margin-top: vwD(70);
        }

        .cards {
          width: vwD(690);
          position: relative;

          .step {
            position: absolute;
            top: 0;
            left: 0;
            transform: translateX(vwD(-190));
            padding: vwD(6) vwD(20);
            box-sizing: border-box;
            width: vwD(142);
            height: vwD(66);
            display: flex;
            justify-content: flex-end;
            align-items: center;

            border: vwD(4) solid #000000;
            box-sizing: border-box;
            border-radius: vwD(16);

            @include Impact();
            font-size: vwD(34);
            line-height: 160%;
            text-transform: uppercase;
            color: #FFF194;
          }

          .inner {
            .card {
              background: #FFFFFF;
              border-radius: vwD(16);
              padding: vwD(20) vwD(30);
              box-sizing: border-box;
              position: relative;

              &:not(:first-child) {
                margin-top: vwD(26);
              }

              .title {
                @include Impact();
                font-size: vwD(24);
                line-height: 160%;
                text-transform: uppercase;
              }

              .text {
                margin-top: vwD(8);
                @include Rubik();
                font-size: vwD(15);
                line-height: 160%;
                color: #424242;
              }

              &:after {
                content: "";
                display: block;
                position: absolute;
                left: vwD(12);
                right: vwD(12);
                bottom: vwD(-10);
                top: vwD(34);
                background: #FFFFFF;
                opacity: 0.5;
                border-radius: vwD(12);
                z-index: -1;
              }
            }


          }


        }

      }

    }
  }

}