@import "../../assets/scss/app.scss";

.burgerMenu {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateY(-105%);
  transition: 0.3s transform;
  z-index: 20;

  &_show {
    transform: translateY(0%);
  }

  .inner {
    background: rgba(255, 255, 255, 0.9);
    backdrop-filter: blur(24px);
    border-radius: 0 0 vw0(30) vw0(30);
    padding-top: vw0(152);
    padding-bottom: vw0(30);
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;

    .links {
      display: flex;
      flex-direction: column;
      align-items: center;

      .link {
        @include Rubik();
        font-size: vw0(20);
        line-height: 160%;
        text-decoration: none;

        &:not(:first-child) {
          margin-top: vw0(30);
        }
      }
    }

    .btnConnect {
      margin-top: vw0(63);
    }

    .account {
      @include onlyMobile() {
        margin-top: vw0(63);
      }
    }

    .socialIcons {
      display: flex;
      margin-top: vw0(30);

      .link {
        display: block;
        text-decoration: none;

        &:not(:first-child) {
          margin-left: vw0(24);
        }

        svg {
          width: vw0(24);
          height: vw0(24);
        }
      }
    }

  }

}