@import "../../assets/scss/app.scss";

.footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 10;
  display: flex;
  flex-direction: column;
  padding-bottom: vw0(20);
  overflow: hidden;

  .backMobile {
    @include onlyMobile() {
      position: absolute;
      z-index: 10;
      top: vw0(14);
      left: 0;
      right: 0;
      bottom: 0;
      background-image: url(../../assets/png/footer/back-mobile.png);
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
    }
  }

  .backDesktop {
    @include onlyDesktop() {
      position: absolute;
      z-index: 10;
      top: vwD(23);
      left: vwD(90);
      right: vwD(90);
      bottom: 0;
      background-image: url(../../assets/png/footer/back-desktop.png);
      background-position: bottom;
      background-size: contain;
      background-repeat: no-repeat;
    }
  }


  @include desktop() {
    padding-bottom: vwD(45);
    padding-left: vwD(200);
    padding-right: vwD(200);
  }

  .logo {
    position: relative;
    z-index: 20;
    align-self: center;
    display: block;
    width: vw0(217);
    height: vw0(52);

    @include desktop() {
      width: vwD(321);
      height: vwD(76);
    }
  }

  .content {
    position: relative;
    z-index: 20;
    display: flex;
    flex-direction: column;
    position: relative;
    margin-top: vw0(16);

    @include desktop() {
      flex-direction: row;
      justify-content: space-between;
      margin-top: vwD(58);
    }

    .links {
      width: vw0(255);
      order: 1;
      display: flex;
      align-self: center;
      justify-content: center;

      @include desktop() {
        order: 2;
        width: auto;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }

      .link {
        text-decoration: none;
        @include Rubik();
        font-size: vw0(14);
        line-height: 160%;
        color: #FFFFFF;
        transition: 0.3s;

        &:hover {
          color: #FFE921;
        }

        @include desktop() {
          font-size: vwD(16);
        }

        &:not(:first-child) {
          margin-left: vw0(20);

          @include desktop() {
            margin-left: vwD(40);
          }
        }
      }
    }

    .socialIcons {
      order: 2;
      display: flex;
      margin-top: vw0(30);
      align-self: center;

      @include desktop() {
        margin-top: 0;
        order: 3;
      }

      .socialLink {

        &:not(:first-child) {
          margin-left: vw0(24);

          @include desktop() {
            margin-left: vwD(24);
          }
        }

        &:hover {
          path {
            fill: #FFE921;
          }
        }

        svg {
          width: vw0(20);
          height: vw0(20);

          & path {
            fill: #FFF;
            transition: 0.3s;
          }

          @include desktop() {
            width: vwD(24);
            height: vwD(24);
          }
        }
      }


    }

    .copy {
      align-self: center;
      order: 3;
      margin-top: vw0(20);
      @include Rubik();
      font-size: vw0(14);
      line-height: 160%;
      color: #FFFFFF;

      @include desktop() {
        margin-top: 0;
        order: 1;
        font-size: vwD(14);
      }
    }

  }


}