@import "../../assets/scss/app.scss";

.btnAccountWrapper {
  position: relative;
  z-index: 999;

  .btnAccount {
    cursor: pointer;
    outline: none;
    background: #FFFFFF;
    border: 1px solid #1E1A3A;
    box-sizing: border-box;
    box-shadow: 1px 4px 0px #332C62;
    display: flex;
    align-items: center;
    border-radius: vw0(28);
    width: vw0(215);
    height: vw0(44);
    padding: 0 vw0(16);

    @include desktop() {
      border-radius: vwD(28);
      width: vwD(215);
      height: vwD(44);
      padding: 0 vwD(16);
    }

    .iconProvider {
      width: vw0(20);
      height: vw0(20);

      @include desktop() {
        width: vwD(20);
        height: vwD(20);
      }

      svg {
        width: vw0(20);
        height: vw0(20);

        @include desktop() {
          width: vwD(20);
          height: vwD(20);
        }
      }
    }

    p {
      margin-left: vw0(10);
      @include Rubik();
      font-size: vw0(14);
      line-height: 160%;
      width: vw0(140);
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;

      @include desktop() {
        margin-left: vwD(10);
        font-size: vwD(14);
        width: vwD(140);
      }
    }

    .iconArrow {
      margin-left: vw0(4);
      width: vw0(10);
      height: vw0(10);
      transition: 0.3s;

      @include desktop() {
        margin-left: vwD(4);
        width: vwD(10);
        height: vwD(10);
      }

      &_showAccount {
        transform: rotate(180deg);
      }

      svg {
        width: vw0(10);
        height: vw0(10);

        @include desktop() {
          width: vwD(10);
          height: vwD(10);
        }
      }
    }

  }

  .account {
    position: absolute;
    z-index: 999;
    width: vw0(302);
    height: vw0(114);
    background: #FFFFFF;
    box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.16);
    border-radius: vw0(10);
    right: 50%;
    bottom: vw0(-10);
    transform: translate(50%, 100%);
    padding: vw0(16) vw0(16);
    box-sizing: border-box;

    @include desktop() {
      transform: translate(0, 100%);
      width: vwD(347);
      height: vwD(132);
      border-radius: vwD(10);
      right: 0;
      bottom: vwD(-12);
      padding: vwD(16) vwD(20);
    }

    .top {
      display: flex;
      align-items: center;
      justify-content: space-between;

      p {
        @include Impact();
        font-size: vw0(20);
        line-height: 150%;
        text-transform: uppercase;

        @include desktop() {
          font-size: vwD(24);
        }
      }

      button {
        cursor: pointer;
        background: none;
        outline: none;
        width: vw0(88);
        height: vw0(27);
        @include center();
        border: 1px solid #E4E4E4;
        box-sizing: border-box;
        border-radius: vw0(6);
        transition: 0.3s;
        @include Rubik();
        font-size: vw0(12);
        line-height: 160%;

        @include desktop() {
          width: vwD(106);
          height: vwD(34);
          border-radius: vwD(6);
          font-size: vwD(14);
        }

        &:hover {
          border: 1px solid #000;
        }
      }
    }

    .infoWrapper {
      margin-top: vw0(10);
      display: flex;

      @include desktop() {
        margin-top: vwD(10);
      }

      .iconWrapper {
        flex-shrink: 0;
        width: vw0(44);
        height: vw0(44);
        border-radius: 50%;
        background: #FFF2A5;
        @include center();

        @include desktop() {
          width: vwD(50);
          height: vwD(50);
        }

        svg {
          width: vw0(24);
          height: vw0(24);

          @include desktop() {
            width: vwD(27);
            height: vwD(27);
          }
        }
      }

      .info {
        margin-left: vw0(9);
        display: flex;
        flex-direction: column;
        justify-content: center;

        @include desktop() {
          margin-left: vwD(14);
        }

        p:first-child {
          @include Rubik();
          font-size: vw0(14);
          line-height: 160%;
          width: vw0(200);
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;

          @include desktop() {
            font-size: vwD(16);
            width: vwD(243);
          }
        }


        p:last-child {
          @include Rubik();
          font-size: vw0(12);
          line-height: 160%;
          color: #929292;

          @include desktop() {
            font-size: vwD(14);
          }
        }
      }


    }
  }
}