@import "../../assets/scss/app.scss";

.mintPage {
  display: flex;
  flex-direction: column;
  align-items: center;

  padding-top: vw0(131);
  padding-bottom: vw0(371);

  background-image: url(../../assets/png/mint/back-mobile.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  @include desktop() {
    padding-top: vwD(155);
    padding-bottom: vwD(796);
    background-image: url(../../assets/png/mint/back-desktop.png);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }

  .status {
    background: #000000;
    height: vw0(46);
    box-sizing: border-box;
    border-radius: vw0(50);
    margin: vw0(30) 0 0;
    width: vw0(320);
    padding: vw0(3) vw0(8);
    display: flex;

    @include desktop() {
      height: vwD(46);
      border-radius: vwD(50);
      margin: vwD(30) 0 0;
      width: vwD(560);
      padding: vwD(3) vwD(8);
    }

    .inner {
      flex-grow: 1;
      border: vw0(2) solid #FFFFFF;
      box-sizing: border-box;
      border-radius: vw0(50);
      display: flex;
      position: relative;

      @include desktop() {
        border: vwD(2) solid #FFFFFF;
        border-radius: vwD(50);
      }

      .box {
        background: #AC4AE5;
        box-shadow: inset -3px -3px 2px rgba(0, 0, 0, 0.11), inset 3px 3px 2px rgba(255, 255, 255, 0.4);
        border-radius: 999px;
        width: vw0(100);

        @include desktop() {
          width: vwD(192);
        }
      }

      .text {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        @include Rubik();
        font-weight: 700;
        font-size: vw0(16);
        line-height: 90%;
        color: #FFFFFF;

        @include desktop() {
          font-size: vwD(18);
        }
      }

    }


  }

  .price {
    @include Impact();
    font-size: vw0(24);
    line-height: 160%;
    text-transform: uppercase;
    margin-top: vw0(29);

    @include desktop() {
      margin-top: vwD(44);
      font-size: vwD(24);
    }
  }

  .countWrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: vw0(24);

    @include desktop() {
      margin-top: vwD(16);
      flex-direction: row;
      justify-content: center;
    }

    .countBlock {
      display: flex;
      justify-content: center;
      align-items: center;

      .count {
        @include Impact();
        font-size: vw0(24);
        line-height: 160%;
        width: vw0(44);
        text-align: center;

        @include desktop() {
          font-size: vwD(24);
          width: vwD(40);
        }
      }
    }

    .btnMint {
      margin-top: vw0(18);

      @include desktop() {
        margin-top: 0;
        margin-left: vwD(20);
      }
    }

  }




}